import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/user/login'
import Home from '@/views/home'
import Test from '@/views/test'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      redirect: '/test',
      children: [
        {
          path: '/test',
          name: 'Test',
          component: Test
        }
      ]
    }
    // {
    //   path: '/test',
    //   name: 'Test',
    //   component: Test
    // }
  ]
})



