<template>
  <div class="loginPage">
    <div class="loginPageBg">
      <div class="systemName">
        <img src="@/assets/images/systemLogoEn.png" alt="">
        <h3>终端管理权限系统</h3>
      </div>
      <el-form ref="loginForm" :model="loginForm" :rules="loginRules" class="loignForm">
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" placeholder="请输入用户名" clearable></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input type="password" v-model="loginForm.password" placeholder="请输入密码" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button id="loginBtn" type="primary" @click="loginUser">登 录</el-button>
        </el-form-item>
        <div class="loginFooter">
          <img src="@/assets/images/companyName.png" alt="">
          <div class="underline"></div>
        </div>
      </el-form>
    </div>
    <div class="iconTrans">
      <span class="iconfont iconwaitaikong"></span>
      <span class="iconfont icontaikongcang"></span>
      <span class="iconfont iconyuzhouxingqiu-14"></span>
      <span class="iconfont iconyuzhouxingqiu-13"></span>
      <span class="iconfont iconditu"></span>
      <span class="iconfont icon33"></span>
      <span class="iconfont iconshebei"></span>
      <span class="iconfont iconyuzhouxingqiu-05"></span>
      <span class="iconfont iconyuzhouxingqiu-15"></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginRules: {
        username: [
          {required: true, message: '请输入用户名', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    loginUser () {
      this.$refs['loginForm'].validate((valid) => {
        if (valid) {
          this.$router.push({path: '/home'})
        } else {
          console.log(2)
        }
      })
    }
  }
}
</script>

<style lang="stylus">
.loginPage
  height 100%
  .loginPageBg
    height 100%
    // background url('~@/assets/images/testBg.jpg') no-repeat center center
    background #f0f2f5
    position relative
    box-sizing border-box
    &:after
      height 100%
      display block
      // background url('~@/assets/images/testBg.jpg') no-repeat center center
      background #f0f2f5
      content ""
      position absolute
      left 0
      right 0
      top 0
      bottom 0
      opacity .9
      filter blur(1px)
    .systemName
      margin-bottom 20px
      font-size 20px
      letter-spacing 2px
      position absolute
      z-index 12
      left 0px
      right 0px
      margin 180px auto 0px
      text-shadow 1px 1px 1px #000, -1px 1px 1px #000, -1px -1px 1px #000, 2px 3px 1px #000
      color white
      img
        width 100%
        max-width 414px
      h3
        margin 0px
    .loignForm
      // background rgba(255, 255, 255, .9)
      // background white
      position absolute
      z-index 10
      left 0
      right 0
      margin 0 auto
      // width 300px
      max-width 300px
      top 260px
      padding 20px
      border-radius 4px
      // box-shadow 2px 2px 20px rgba(0, 0, 0, .4), -2px 2px 20px rgba(0, 0, 0, .4)
      #loginBtn
        width 100%
        background #43495f
        border-color #43495f
        border-radius 10px
        &:active
          box-shadow 2px 2px 10px rgba(0, 0, 0, .2) inset, -2px 2px 10px rgba(0, 0, 0, .2) inset
      .el-form-item
        margin-bottom 25px
      .el-input
        .el-input__inner
          border-radius 10px
          height 46px
          &:focus
            border-color #43495f
            border-width 2px
      .el-form-item.is-error .el-input__inner
        border-width 2px
      .el-form-item__error
        padding-left 18px
      .loginFooter
        img
          background #f0f2f5
          width 60px
          padding 0px 10px
        .underline
          height 2px
          background #b7b2b2
          margin-top -11px
          border-radius 50%
  .iconTrans span
    position absolute
    z-index 6
    &.iconwaitaikong
      font-size 70px
      top 7%
      left 6%
      animation icon1 2s linear infinite
    &.icontaikongcang
      font-size 30px
      bottom 7%
      left 6%
      animation icon2 5s linear infinite
    &.iconyuzhouxingqiu-14
      font-size 50px
      top 25%
      right 16%
      animation icon3 3s linear infinite reverse
    &.iconyuzhouxingqiu-13
      font-size 50px
      top 6%
      right 40%
      animation icon4 2s linear infinite
    &.iconditu
      font-size 20px
      bottom 20%
      left 40%
      font-weight bold
    &.icon33
      font-size 20px
      bottom 80%
      right 10%
    &.iconshebei
      font-size 20px
      bottom 25%
      right 10%
    &.iconyuzhouxingqiu-05
      font-size 40px
      bottom 30%
      right 45%
      animation icon5 3s linear infinite
    &.iconyuzhouxingqiu-15
      font-size 40px
      bottom 10%
      right 25%
      animation icon6 6s linear infinite

@keyframes icon1 {
  0% {
    opacity .5
  }
  50% {
    opacity .8
  }
  100% {
    opacity .9
  }
}
@keyframes icon2 {
  0% {
    transform translateX(0px)
  }
  50% {
    transform translateY(-150px) translateX(30px)
  }
  75% {
    transform translateX(-30px) translateY(-120px)
  }
  100% {
    transform translateX(0px) translateY(0px)
  }
}
@keyframes icon3 {
  0% {
    transform translateX(0px)
  }
  50% {
    transform translateY(-50px) translateX(30px)
  }
  75% {
    transform translateX(-30px) translateY(-20px)
  }
  100% {
    transform translateX(0px) translateY(0px)
  }
}
@keyframes icon4 {
  0% {
    transform rotate(0deg)
  }
  25% {
    transform rotate(90deg)
  }
  50% {
    transform rotate(180deg)
  }
  75% {
    transform rotate(270px)
  }
  100% {
    transform rotate(360px)
  }
}
@keyframes icon5 {
  0% {
    transform translateX(0px)
  }
  50% {
    transform translateY(-100px) translateX(30px)
    opacity .5
  }
  75% {
    transform translateX(-30px) translateY(-20px)
    opacity .7
  }
  100% {
    transform translateX(0px) translateY(0px)
    opacity 1
  }
}
@keyframes icon6 {
  0% {
    transform translateX(0px) rotate(30deg)
  }
  50% {
    transform translateY(-50px) rotate(0deg)
  }
  75% {
    transform translateX(-30px) rotate(-30deg)
  }
  100% {
    transform translateX(0px) rotate(30deg)
  }
}
</style>



