<template>
  <div class="homePage">
    <el-container class="homeLayout">
      <el-header>
        <header-bar></header-bar>
      </el-header>
      <el-container>
        <el-aside width="250px">
          <sider-bar></sider-bar>
        </el-aside>
        <el-main>
          <transition name="slide-fade">
            <div class="homeContent">
              <div class="homeBreadCrumb">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                  <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                  <el-breadcrumb-item>活动管理</el-breadcrumb-item>
                  <el-breadcrumb-item>活动列表</el-breadcrumb-item>
                  <el-breadcrumb-item>活动详情</el-breadcrumb-item>
                </el-breadcrumb>
              </div>
              <router-view/>
            </div>
          </transition>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import headerBar from '@/components/headerBar'
import siderBar from '@/components/siderBar'
export default {
  name: 'home',
  components: {
    headerBar,
    siderBar
  }
}
</script>

<style lang="stylus">
.homePage
  height 100%
  .homeLayout
    height 100%
    .el-header, .el-footer
      background-color #B3C0D1
      color #333
      text-align center
      line-height 60px
      box-shadow 1px 1px 4px rgba(0, 0, 0, .8)
    .el-aside
      background-color #D3DCE6
      color #333
      text-align center
      // line-height 200px
      overflow hidden
    .el-main
      background-color #E9EEF3
      color #333
      text-align center
      padding 10px
      padding-bottom 30px
      .homeContent
        background white
        .homeBreadCrumb
          padding 10px 0px
          padding-left 10px
          border-bottom 1px solid #eee
</style>



